<template>
  <div style="width: 80%; margin: auto">
    <el-row type="flex" justify="end" style="margin-top: 10px; margin-bottom: 20px">
      <el-button type="text" size="mini" icon="el-icon-upload" circle @click="uploadVisible = true">{{ $t("message.top_upload_file") }}</el-button>
      <el-button type="text" size="mini" icon="el-icon-setting" circle @click="showConfig">{{ $t("message.top_config") }}</el-button>
      <el-button type="text" size="mini" icon="el-icon-reading" circle @click="goHelp">{{ $t("message.top_help") }}</el-button>
    </el-row>
    <!-- 查询域 -->
    <el-row type="flex" justify="center" style="margin-top: 40px; margin-bottom: 40px">
      <el-col :xs="21" :sm="19" :md="16" :lg="12" :xl="10">
        <el-autocomplete class="inline-input" v-model="queryParams.keyWord" @input="handleDebounce"
          @keyup.enter.native="query(queryParams.keyWord)" :fetch-suggestions="showHistory" 
          :placeholder="$t('message.text_search_tips')"
          prefix-icon="el-icon-search" @select="handleSearchSelect" style="width: 100%"></el-autocomplete>
      </el-col>
      <el-col :xs="3" :sm="3" :md="3" :lg="3" :xl="3">
        <el-button type="primary" @click="query(queryParams.keyWord)">{{ $t('message.search') }}</el-button>
      </el-col>
    </el-row>

    <el-row type="flex" justify="end" style="margin-bottom: 0px">
      <el-radio-group v-model="filterTypeRadio" @change="query(queryParams.keyWord)" style="margin-right: 20px"
        size="mini">
        <el-radio-button label="ALL">{{ $t('message.tag_all') }}</el-radio-button>
        <el-radio-button label="视频">{{ $t('message.tag_video') }}</el-radio-button>
        <el-radio-button label="音频">{{ $t('message.tag_audio') }}</el-radio-button>
        <el-radio-button label="图片">{{ $t('message.tag_image') }}</el-radio-button>
        <el-radio-button label="文档">{{ $t('message.tag_document') }}</el-radio-button>
        <el-radio-button label="文本">{{ $t('message.tag_text') }}</el-radio-button>
        <el-radio-button label="压缩包">{{ $t('message.tag_archive') }}</el-radio-button>
        <el-radio-button label="可执行">{{ $t('message.tag_executable') }}</el-radio-button>
        <el-radio-button label="其它">{{ $t('message.tag_other') }}</el-radio-button>
      </el-radio-group>
    </el-row>
    <!-- 表格 -->
    <el-table :data="pager.beans" @sort-change="sortChange" ref="Table" @row-click="selectRow" @row-dblclick="dblOpen"
      style="font-size: 12px" stripe highlight-current-row v-loading="$store.state.loading"
      :default-sort="{ prop: 'fileModifyTime', order: 'descending' }">
      <el-table-column v-if="showSequence" type="index" min-width="50" :label="$t('message.header_sequence')" align="center">
      </el-table-column>
      <el-table-column prop="fileName" :label="$t('message.header_file_name')" min-width="250" sortable align="left">
        <template slot-scope="scope">
          <el-tooltip class="item" effect="light" :content="scope.row.fileName" placement="bottom-start"
            :open-delay="openDelay">
            <el-tag type="info" effect="plain" size="medium">
              <template v-if="scope.row.fileType == '文件夹'"><i class="el-icon-folder-opened"
                  style="color: #87cefa"></i></template>
              <template v-if="scope.row.fileType == '视频'"><i class="el-icon-video-camera"
                  style="color: blue"></i></template>
              <template v-if="scope.row.fileType == '音频'"><i class="el-icon-headset" style="color: blue"></i></template>
              <template v-if="scope.row.fileType == '图片'"><i class="el-icon-picture-outline"
                  style="color: blue"></i></template>
              <template v-if="scope.row.fileType == '文档'"><i class="el-icon-document" style="color: grey"></i></template>
              <template v-if="scope.row.fileType == '压缩包'"><i class="el-icon-collection"
                  style="color: black"></i></template>
              <template v-if="scope.row.fileType == '文本'"><i class="el-icon-reading" style="color: grey"></i></template>
              <template v-if="scope.row.fileType == '可执行'"><i class="el-icon-position"
                  style="color: lightgreen"></i></template>
              <template v-if="scope.row.fileType == '-'"><i class="el-icon-info" style="color: lightgrey"></i></template>
              <template v-if="scope.row.fileType == '其它'"><i class="el-icon-warning-outline"
                  style="color: lightgrey"></i></template>
              {{ scope.row.fileName }}
            </el-tag>
          </el-tooltip>
        </template>
      </el-table-column>
      <el-table-column prop="path" :label="$t('message.header_path')" min-width="100" sortable align="left">
        <template slot-scope="scope">
          <el-tooltip class="item" effect="light" :content="scope.row.path" placement="bottom-start"
            :open-delay="openDelay">
            <span style="
                display: inline-block;
                white-space: nowrap;
                word-break: keep-all;
              ">
              {{ scope.row.path }}
            </span>
          </el-tooltip>
        </template>
      </el-table-column>
      <el-table-column prop="fileType" :label="$t('message.header_type')" sortable min-width="80" align="center">
        <template slot-scope="scope">
          <span style="
              display: inline-block;
              white-space: nowrap;
              word-break: keep-all;
            ">
            {{ $t("message."+scope.row.fileType) }}
          </span>
        </template>
      </el-table-column>
      <el-table-column prop="fileSize" :label="$t('message.header_size')" sortable min-width="90" align="center">
        <template slot-scope="scope">
          <span style="
              display: inline-block;
              white-space: nowrap;
              word-break: keep-all;
            ">
            {{ scope.row.fileSize == 0 ? "-" : convert(scope.row.fileSize) }}
          </span>
        </template>
      </el-table-column>
      <el-table-column prop="fileModifyTime" :label="$t('message.header_modify_time')" sortable min-width="160" align="center">
        <template slot-scope="scope">
          <span style="
              display: inline-block;
              white-space: nowrap;
              word-break: keep-all;
            ">
            {{ scope.row.fileModifyTime }}
          </span>
        </template>
      </el-table-column>
      <el-table-column label="" width="50" align="center" fixed="right">
        <template slot-scope="scope">
          <el-dropdown @command="handleCommand" trigger="click" size="mini">
            <span class="el-dropdown-link">
              <i class="el-icon-arrow-down el-icon--right"></i>
            </span>
            <el-dropdown-menu slot="dropdown">
              <el-dropdown-item icon="el-icon-folder-opened" v-if="scope.row.remote == false"
                command="open">{{ $t("message.menu_open") }}</el-dropdown-item>
              <el-dropdown-item icon="el-icon-folder" v-if="scope.row.remote == false"
                command="openDIR">{{ $t("message.menu_open_dir") }}</el-dropdown-item>
              <el-dropdown-item icon="el-icon-search" command="searchFolder">{{ $t("message.menu_search_in_dir") }}</el-dropdown-item>
              <el-dropdown-item icon="el-icon-picture" v-if="scope.row.fileType == '图片'"
                command="viewFile">{{ $t("message.menu_view") }}</el-dropdown-item>
              <el-dropdown-item icon="el-icon-video-play" v-if="scope.row.fileType == '视频' || scope.row.fileType == '音频'
                " command="viewFile">{{ $t("message.menu_play") }}</el-dropdown-item>
              <el-dropdown-item icon="el-icon-attract" v-if="scope.row.fileType == '视频' || scope.row.fileType == '音频'
                " command="httpStream">{{ $t("message.menu_http_stream") }}</el-dropdown-item>
              <el-dropdown-item icon="el-icon-view" v-if="scope.row.fileType == '文档' || scope.row.fileType == '文本'
                " command="viewFile">{{ $t("message.menu_preview") }}</el-dropdown-item>
              <el-dropdown-item icon="el-icon-download" command="downloadFile">{{ $t("message.menu_download") }}</el-dropdown-item>
              <el-dropdown-item icon="el-icon-share" command="shareFile">{{ $t("message.menu_share") }}</el-dropdown-item>
              <el-dropdown-item icon="el-icon-key" command="shareFile1">{{ $t("message.menu_share_with_fetch_code") }}</el-dropdown-item>
            </el-dropdown-menu>
          </el-dropdown>
        </template>
      </el-table-column>
    </el-table>
    <el-tag v-if="showMoreVisible"> {{ $t("message.bottom_text_tip") }} </el-tag>
    <!-- video player -->
    <el-dialog :title="videoTitle" width="80%" heigth="80%" @open="createVideo(currentRow)" :show-close="false" @close="destoryVideo"
      :visible.sync="videoVisible">
      <div class="centered-div">
        <video id="videoElement" heigth="80%" width="80%" @ended="videoEnded" controls>
          your browser does not support html5 video
        </video>
      </div>
      <div slot="footer" class="dialog-footer">
        <el-button size="medium" @click="videoControl('previous')" icon="el-icon-d-arrow-left"></el-button>
        <el-button size="medium" @click="videoControl('next')" icon="el-icon-d-arrow-right"></el-button>
        <el-button size="medium" @click="videoControl('close')" icon="el-icon-close"></el-button>
      </div>
    </el-dialog>
    <!--音频播放器-->
    <el-dialog width="80%" @open="createAudio(false,'aplayer')" :show-close="false" @close="closeAudioDialog()" :visible.sync="audioVisible">
      <div id="aplayer"></div>
      <div slot="footer" class="dialog-footer">
        <el-button size="medium" @click="audioControl('previous')" icon="el-icon-d-arrow-left"></el-button>
        <el-button size="medium" @click="audioControl('next')" icon="el-icon-d-arrow-right"></el-button>
        <el-button size="medium" @click="audioControl('close')" icon="el-icon-close"></el-button>
      </div>
    </el-dialog>
    <!-- 系统初始化对话框 -->
    <el-dialog width="45%"  style="background-color: lightgray;" :title="$t('message.init_dialog_form_title')" :close-on-click-modal=false :close-on-press-escape=false :show-close=false :visible.sync="initConfigVisible">
      <el-form>
        <el-form-item :label="$t('message.init_dialog_form_domain')">

          <el-autocomplete :placeholder="$t('message.init_dialog_form_domain_tip')" v-model="initParam.domain" :fetch-suggestions="queryIntranetIps" style="width: 75%;">
            <template slot="prepend">Http(s)://</template>
          </el-autocomplete>
        </el-form-item>
        <el-form-item :label="$t('message.init_dialog_form_password')">
          <el-col :span="10">
            <el-input size="mini" type="password" v-model="initParam.newPass" show-password placeholder="请设置初始密码">
            </el-input>
          </el-col>
        </el-form-item>
        <el-form-item :label="$t('message.init_dialog_form_scanAll')">
          <el-checkbox v-model="initParam.scanALL"></el-checkbox>
        </el-form-item>
        <el-button type="primary" size="mini" @click="initSystem">{{$t('message.init_dialog_form_submit')}}</el-button>
      </el-form>
    </el-dialog>
    <!-- 上传文件对话框 -->
    <el-dialog :title="$t('message.upload_dialog_upload')" :visible.sync="uploadVisible">
      <div class="centered-div">
        <el-upload
          drag
          action="/file/upload"
          multiple>
          <i class="el-icon-upload"></i>
          <div class="el-upload__text">{{ $t('message.upload_dialog_tip') }}<em>{{ $t('message.upload_dialog_tip2') }}</em></div>
          <div class="el-upload__tip" slot="tip">{{$t('message.upload_dialog_tip3')}} <em>upload</em> {{ $t('message.upload_dialog_tip4') }}</div>
        </el-upload>
      </div>
    </el-dialog>
    <!-- 设置对话框 -->
    <el-dialog :title="$t('message.config_dialog_title')" @close="indexToDel = null" :visible.sync="configVisible">
      <!-- 添加索引对话框 -->
      <el-dialog :title="$t('message.config_dialog_add_index_add_index')" :visible.sync="addIndexVisible" append-to-body>
        <el-form>
          <el-form-item :label="$t('message.config_dialog_add_index_path')">
            <el-col :xs="24" :sm="24" :md="16" :lg="16" :xl="16">
              <el-input size="mini" v-model="addIndexParams.path"
                :placeholder="addIndexParams.type == 'LOCAL' ? 'D:\\path' : 'smb://host/path'">
              </el-input>
            </el-col>
          </el-form-item>

          <el-form-item :label="$t('message.config_dialog_add_index_account')" v-if="addIndexParams.type != 'LOCAL'">
            <el-col :xs="12" :sm="12" :md="6" :lg="6" :xl="6">
              <el-input size="mini" v-model="addIndexParams.userName" :placeholder="$t('message.config_dialog_add_index_account_tip')">
              </el-input>
            </el-col>
          </el-form-item>
          <el-form-item :label="$t('message.config_dialog_add_index_password')" v-if="addIndexParams.type != 'LOCAL'">
            <el-col :xs="12" :sm="12" :md="6" :lg="6" :xl="6">
              <el-input size="mini" v-model="addIndexParams.passwd" show-password type="password" :placeholder="$t('message.config_dialog_add_index_password_tip')">
              </el-input>
            </el-col>
          </el-form-item>
          <el-form-item v-if="addIndexParams.type == 'LOCAL'">
            <el-switch v-model="addIndexParams.listen" :inactive-text="$t('message.config_dialog_add_index_listen')"></el-switch>
          </el-form-item>
          <el-form-item :label="$t('message.config_dialog_add_index_type')">
            <el-select size="mini" v-model="addIndexParams.type" style="width: 20%" placeholder="">
              <el-option :label="$t('message.config_dialog_add_index_local')" value="LOCAL"></el-option>
              <el-option label="Samba" value="Samba"></el-option>
            </el-select>
          </el-form-item>

          <el-form-item>
            <el-button type="primary" size="mini" @click="createIndex(addIndexParams)">{{$t('message.config_dialog_add_index_button_submit')}}</el-button>
            <el-button @click="resetAddIndexForm" size="mini">{{$t('message.config_dialog_add_index_button_reset')}}</el-button>
          </el-form-item>
        </el-form>
      </el-dialog>
      <el-tabs v-model="activeTab" style="height: 400px" @tab-click="indexLoad">
        <!-- 基本设置 -->
        <el-tab-pane :label="$t('message.config_dialog_base_title')" name="searchConfigTab">
          <el-form>
            <el-form-item :label="$t('message.config_dialog_base_page_size')">
              <el-select v-model="queryParams.top" style="width: 75px; margin-right: 10px" allow-create
                @change="refreshPage();configVisible=false" size="mini">
                <el-option label="20" value="20"></el-option>
                <el-option label="50" value="50"></el-option>
                <el-option label="200" value="200"></el-option>
                <el-option label="1000" value="1000"></el-option>
              </el-select>
              <el-switch v-model="showSequence" @change="configVisible = false" :inactive-text="$t('message.config_dialog_base_row_number')">
              </el-switch>
            
            </el-form-item>
            <el-form-item :label="$t('message.config_dialog_base_language')">
              <el-select v-model="language" :placeholder="$t('message.config_dialog_base_language_tips')" @change="changeLocale" size="mini">
                <el-option
                  v-for="item in langOptions"
                  :key="item.value"
                  :label="item.label"
                  :value="item.value">
                </el-option>
              </el-select>
            </el-form-item>
  
            <el-form-item :label="$t('message.config_dialog_base_domain')">
              <el-autocomplete :placeholder="$t('message.config_dialog_base_domain_tip')" v-model="baseConfig.domain" :fetch-suggestions="queryIntranetIps" style="width: 75%;">
                <template slot="prepend">Http(s)://</template>
              </el-autocomplete>
            </el-form-item>
            <!-- 其它文件设置 -->
            <el-form-item :label="$t('message.config_dialog_base_other_file')">
              <el-tag :key="tag" v-for="tag in baseConfig.otherFiles" closable :disable-transitions="false"
                @close="handleClose(tag)">
                {{ tag }}
              </el-tag>
              <el-input class="input-new-tag" v-if="otherFileInputVisible" v-model="otherFileInputValue"
                ref="saveTagInput" size="small" @keyup.enter.native="handleInputConfirm" @blur="handleInputConfirm">
              </el-input>
              <el-button v-else class="button-new-tag" size="small" @click="showInput">{{$t('message.config_dialog_base_add_suffix')}}</el-button>
            </el-form-item>
            <el-form-item>
              <span style="font-size: small;color: gray;">{{$t('message.config_dialog_base_note')}}</span>
            </el-form-item>
            <el-form-item>
              <el-button type="primary" size="mini" @click="handleUpdateBaseConfig()">{{$t('message.config_dialog_base_submit')}}</el-button>
            </el-form-item>
          </el-form>
        </el-tab-pane>
        <!-- 索引目录 -->
        <el-tab-pane :label="$t('message.config_dialog_index_title')" name="indexDirectoryTab">
          <el-table lable :data="indexPager.beans" :default-sort="{ prop: 'path', order: 'ascending' }" height="350px"
            style="width: 100%; font-size: 10px">
            <el-table-column min-width="150" :label="$t('message.config_dialog_index_path')" sortable align="left" prop="path">
              <template slot-scope="scope">
                <span style="
                    display: inline-block;
                    white-space: nowrap;
                    word-break: keep-all;
                  ">
                  {{ scope.row.path }}
                </span>
              </template>
            </el-table-column>
            <el-table-column :label="$t('message.config_dialog_index_type')" min-width="60" align="center" prop="isRemote">
              <template slot-scope="scope">
                <el-tag size="mini">{{
                  scope.row.isRemote ? $t('message.config_dialog_index_remote') : $t('message.config_dialog_index_local')
                }}</el-tag>
              </template>
            </el-table-column>
            <el-table-column :label="$t('message.config_dialog_index_listen_change')" min-width="60" align="center" prop="listen">
              <template slot-scope="scope">
                <el-checkbox v-if="!scope.row.isRemote" v-model="scope.row.listen"
                  @change="handleChangeListen(scope.row)"></el-checkbox>
                <el-checkbox v-if="scope.row.isRemote" v-model="scope.row.listen" disabled></el-checkbox>
              </template>
            </el-table-column>
            <el-table-column :label="$t('message.config_dialog_index_files_count')" min-width="65" align="center" prop="fileCount">
            </el-table-column>
            <el-table-column min-width="120" align="center" :label="$t('message.config_dialog_index_index_time')" prop="indexTime">
              <template slot-scope="scope">
                <span style="
                    display: inline-block;
                    white-space: nowrap;
                    word-break: keep-all;
                  ">
                  {{ scope.row.indexTime }}
                </span>
              </template>
            </el-table-column>
            <el-table-column align="center" width="60" fixed="right">
              <template slot="header">
                <el-button type="text" icon="el-icon-plus" @click="showAddIndex"></el-button>
              </template>
              <template slot-scope="scope">
                <el-button type="text" size="mini" @click="handleReIndex(scope.row)" :loading="scope.row.loading"
                  icon="el-icon-refresh-right"></el-button>
                <!-- 删除索引-->
                <el-button type="text" size="mini" @click="handleRemoveIndex(scope.row)" icon="el-icon-minus"></el-button>
              </template>
            </el-table-column>
          </el-table>
          <span style="font-size: small;color: gray;">{{$t('message.config_dialog_index_note')}}</span>
        </el-tab-pane>

        <!-- 修改密码 -->
        <el-tab-pane :label="$t('message.config_dialog_password_title')" name="updateUserTab">
          <el-form>
            <el-form-item :label="$t('message.config_dialog_password_old_pass')">
              <el-col :span="16">
                <el-input size="mini" type="password" show-password v-model="updateUserParams.oldPass">
                </el-input>
              </el-col>
            </el-form-item>
            <el-form-item :label="$t('message.config_dialog_password_new_pass')">
              <el-col :span="16">
                <el-input size="mini" type="password" show-password v-model="updateUserParams.newPass">
                </el-input>
              </el-col>
            </el-form-item>
            <el-form-item>
              <el-button type="primary" size="mini" @click="updateUser(updateUserParams)">{{$t('message.config_dialog_password_submit')}}</el-button>
              <el-button @click="resetUpdateUserForm" size="mini">{{$t('message.config_dialog_password_reset')}}</el-button>
            </el-form-item>
          </el-form>
        </el-tab-pane>
      </el-tabs>
    </el-dialog>
    <div id="aplayer_mini"></div>
  </div>
</template>
<style>
.el-tag+.el-tag {
  margin-left: 10px;
}

.button-new-tag {
  margin-left: 10px;
  height: 32px;
  line-height: 30px;
  padding-top: 0;
  padding-bottom: 0;
}

.input-new-tag {
  width: 90px;
  margin-left: 10px;
  vertical-align: bottom;
}
.centered-div {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  height: 100%; 
}
.input-with-select .el-input-group__prepend {
  background-color: #fff;
}

.dialog-footer .step {
  color: #000;
}

.dialog-footer .stepBtn {
  color: #000;
}
</style>
<script>
import "viewerjs/dist/viewer.css";
import Viewer from "viewerjs";
import flvjs from "flv.js";
import "APlayer/dist/APlayer.min.css";
import APlayer from "APlayer";
import elementEnLocale from 'element-ui/lib/locale/lang/en';
import elementZhLocale from 'element-ui/lib/locale/lang/zh-CN';
import elementLocale from 'element-ui/lib/locale'
function Debounce(fn) {
  let delay = 500;
  let timer;
  return function () {
    let args = arguments;
    if (timer) {
      clearTimeout(timer);
    }
    timer = setTimeout(() => {
      timer = null;
      fn.apply(this, args);
    }, delay);
  };
}

export default {
  name: "index",
  data() {
    return {
      langOptions:[],
      language: this.$t('message.language'),
      initParam: {scanALL: false,newPass: "", domain: ""},
      initConfigVisible: false,
      baseConfig: { otherFiles: [], domain: "" },
      otherFileInputVisible: false,
      turnToMini: true,
      otherFileInputValue: '',
      filterTypeRadio: "ALL",
      searchHistory: [],
      activeTab: "searchConfigTab",
      configVisible: false,
      uploadVisible: false,
      videoVisible: false,
      audioVisible: false,
      videoPlayer: null,
      audioPlayer: null,
      indexToDel: null,
      videoList: [],
      videoTitle: this.$t('message.data_video_title'),
      showMoreVisible: false,
      showSequence: true,
      addIndexVisible: false,
      queryParams: { top: 20, keyWord: "" },
      addIndexParams: { type: "LOCAL" },
      updateUserParams: {},
      currentRow: {},
      currentIndex: 0,
      openDelay: 1000,
      pager: { beans: [] },
      indexPager: { beans: [] },
    };
  },
  mounted() {
    this.getBaseConfig();
    let locale = localStorage.getItem('language','zh');
    this.changeLocale(locale);
  },
  methods: {
    changeLocale(locale){
      this.$i18n.locale = locale;
      if(locale == "zh"){
        elementLocale.use(elementZhLocale);
        localStorage.setItem('language','zh')
      }else if(locale == "en"){
        elementLocale.use(elementEnLocale);
        localStorage.setItem('language','en')
      }
      this.langOptions=[{
          value: 'en',
          label: this.$t('message.lang_en')
        }, {
          value: 'zh',
          label: this.$t('message.lang_zh')
        }, {
          value: 'de',
          label: this.$t('message.lang_de')
        }, {
          value: 'fr',
          label: this.$t('message.lang_fr')
        }, {
          value: 'ja',
          label: this.$t('message.lang_ja')
        }, {
          value: 'ko',
          label: this.$t('message.lang_ko')
        }, {
          value: 'ru',
          label: this.$t('message.lang_ru')
        }, {
          value: 'tw',
          label: this.$t('message.lang_tw')
        }];
      this.configVisible = false;
    },
    queryIntranetIps(queryString, cb){
      this.$http
        .get("/admin/getIntranetIps")
        .then((res) => {
          if (res.data.code == 0) {
            let ips = res.data.beans;
            var results = queryString ? ips.filter(this.createFilter(queryString)) : ips;
            cb(results)
          }
        });
    },
    createFilter(queryString) {
        return (ip) => {
          return (ip.value.toLowerCase().indexOf(queryString.toLowerCase()) === 0);
        };
      },
    initSystem(){
      this.$http
        .post(
          "/admin/initConfig",
          "newPass=" + (this.initParam.newPass == "" ? undefined : this.initParam.newPass)
          + "&domain=" + (this.initParam.domain== "" ? undefined : this.initParam.domain)
          +"&scanALL="+this.initParam.scanALL
        )
        .then((res) => {
          if(res.data.code == 0){
            this.initConfigVisible = false
            this.getBaseConfig();
          }
        });
    },
    goHelp() {
      window.open('https://gitee.com/polarlb/everything')
    },
    handleChangeListen(params) {
      this.$http
        .post("/index/update",
          "id=" + params.id +
          "&listen=" + params.listen
        )
        .then((res) => {
          if (res.data.code == 0) {
            this.$notify({
              title: this.$t('message.tip_success'),
              message: this.$t('message.tip_listen_change_success'),
              type: 'success',
              duration: 0
            });
          }
        });


    },
    handleClose(tag) {
      this.baseConfig.otherFiles.splice(this.baseConfig.otherFiles.indexOf(tag), 1);
    },

    showInput() {
      this.otherFileInputVisible = true;
      this.$nextTick(_ => {
        this.$refs.saveTagInput.$refs.input.focus();
      });
    },
    getBaseConfig() {
      this.$http
        .get("/admin/getBaseConfig")
        .then((res) => {
          if (res.data.code == 0) {
            if(!res.data.bean.init){
              this.initConfigVisible=true
              this.initParam.domain=res.data.bean.domain
            }else{
              this.initSearchHistory();
              this.queryRecent();
            }
            if (res.data.bean.otherFiles) {
              this.baseConfig.otherFiles = res.data.bean.otherFiles.split(",")
            }
            this.baseConfig.domain = res.data.bean.domain
          }
        });
    },
    handleUpdateBaseConfig() {
      this.$http
        .post(
          "/admin/setBaseConfig",
          "otherFiles=" + this.baseConfig.otherFiles + "&domain=" + this.baseConfig.domain+"&init=true"
        )
        .then((res) => {
          this.$message({
            message: res.data.msg,
            type: res.data.code == -1 ? "error" : "success",
          });
        });
    },
    handleInputConfirm() {
      let otherFileInputValue = this.otherFileInputValue;
      if (otherFileInputValue) {
        this.baseConfig.otherFiles.push(otherFileInputValue);
      }
      this.otherFileInputVisible = false;
      this.otherFileInputValue = '';
    },

    audioControl(command) {
      if (command == "next") {
        if (this.audioPlayer) this.audioPlayer.skipForward();
      } else if (command == "previous") {
        if (this.audioPlayer) this.audioPlayer.skipBack();
      } else if (command == "close") {
        this.turnToMini = false;
        this.audioVisible = false;
      }
    },
    destoryAudio(){
      if(this.audioPlayer){
        this.audioPlayer.list.clear();
        this.audioPlayer.destroy();
        this.audioPlayer=null;
      }
    },
    destoryVideo() {
      if (this.videoPlayer) this.videoPlayer.destroy();
    },
    closeAudioDialog() {
      if(this.turnToMini){
        this.createAudio(true,"aplayer_mini");
      }else{
        this.destoryAudio();
      }
      this.turnToMini=true;
    },
    getFileSuffix(fileName) {
      var index = fileName.lastIndexOf(".");
      var suffix = fileName.substr(index + 1);
      return suffix;
    },
    createImageList() {
      var listEle = document.createElement("ul");
      var img = document.createElement("img");
      img.src = "/file/viewFile?id=" + this.currentRow.id;
      img.alt = this.currentRow.fileName;
      listEle.append(img);
      for (var i = 0; i < this.$refs.Table.tableData.length; i++) {
        if (
          this.$refs.Table.tableData[i].fileType != "图片" ||
          this.$refs.Table.tableData[i].id == this.currentRow.id
        )
          continue;
        var img = document.createElement("img");
        img.src = "/file/viewFile?id=" + this.$refs.Table.tableData[i].id;
        img.alt = this.$refs.Table.tableData[i].fileName;
        listEle.append(img);
      }
      const gallery = new Viewer(listEle);
      gallery.show();
    },
    createAudio(fixed,element_id) {
      var audioElement = document.getElementById(element_id);
      if (!audioElement) {
        setTimeout(() => {
          this.createAudio(fixed,element_id);
        }, 1000);
        return;
      }
      if(this.audioPlayer){
        this.audioPlayer.list.clear();
        this.audioPlayer.destroy();
        this.audioPlayer=null;
      }
      var audioName = this.currentRow.fileName;
      this.audioPlayer = new APlayer({
        container: audioElement,
        autoplay: true,
        fixed: fixed,
        listMaxHeight: "250px",
        audio: [
          {
            name: audioName,
            artist: this.$t('message.audio_artist'),
            url: "/file/viewFile?id=" + this.currentRow.id,
            cover: "/img/audio_default.png",
          },
        ],
      });
      for (var i = 0; i < this.$refs.Table.tableData.length; i++) {
        if (
          this.$refs.Table.tableData[i].fileType != "音频" ||
          this.$refs.Table.tableData[i].id == this.currentRow.id
        )
          continue;
        var audio = this.$refs.Table.tableData[i];
        this.audioPlayer.list.add({
          name: audio.fileName,
          artist: this.$t('message.audio_artist'),
          url: "/file/viewFile?id=" + audio.id,
          cover: "/img/audio_default.png",
        });
      }
    },
    videoEnded() {
      this.videoControl("next");
    },
    videoControl(command) {
      if (command == "next") {
        if (this.videoPlayer) {
          if (this.currentIndex + 1 > this.$refs.Table.tableData.length - 1)
            return;
          this.currentIndex++;
          if (
            this.$refs.Table.tableData[this.currentIndex].fileType != "视频"
          ) {
            this.videoControl(command);
            return;
          }
          this.destoryVideo();
          this.createVideo(this.$refs.Table.tableData[this.currentIndex]);
        }
      } else if (command == "previous") {
        if (this.currentIndex - 1 < 0) return;
        this.currentIndex--;
        if (this.$refs.Table.tableData[this.currentIndex].fileType != "视频") {
          this.videoControl(command);
          return;
        }
        this.destoryVideo();
        this.createVideo(this.$refs.Table.tableData[this.currentIndex]);
      } else if (command == "close") {
        this.videoVisible = false;
      }
    },
    createVideo(row) {
      var videoElement = document.getElementById("videoElement");
      var videoName = row.fileName;
      this.videoTitle = videoName;
      if (!videoElement) {
        setTimeout(() => {
          this.createVideo(row);
        }, 1000);
        return;
      }
      if (flvjs.isSupported()) {
        var videoURL = "/file/viewFile?id=" + row.id;
        this.videoPlayer = flvjs.createPlayer({
          type: this.getFileSuffix(videoName),
          url: videoURL,
        });
        this.videoPlayer.attachMediaElement(videoElement);
        this.videoPlayer.load();
        this.videoPlayer.play();
      }
    },
    updateUser(params) {
      if (!params.oldPass) return;
      if (!params.newPass) return;
      this.$http
        .post(
          "/admin/updatePassword",
          "oldPass=" + params.oldPass + "&newPass=" + params.newPass
        )
        .then((res) => {
          this.$message({
            message: res.data.msg,
            type: res.data.code == -1 ? "error" : "success",
          });
          this.resetUpdateUserForm();
        });
    },
    copyText(text) {
      var tag = document.createElement("input");
      tag.setAttribute("id", "cp_hgz_input");
      tag.value = text;
      document.getElementsByTagName("body")[0].appendChild(tag);
      document.getElementById("cp_hgz_input").select();
      document.execCommand("copy");
      document.getElementById("cp_hgz_input").remove();
      this.$message({
        message: this.$t('message.link_copy_success'),
        type: "success",
      });
    },
    handleSearchSelect(item) {
      this.queryParams.keyWord = item.value;
    },
    showHistory(queryStr, cb) {
      var history = [];
      for (var i = 0; i < this.searchHistory.length; i++) {
        history.push({ value: this.searchHistory[i] });
      }
      var r = queryStr ? history.filter(this.createFilter(queryStr)) : history;
      cb(r);
    },
    putSearchHistory(key) {
      if (!key) return;
      if (!this.searchHistory.includes(key)) {
        this.searchHistory.unshift(key);
      } else {
        let i = this.searchHistory.indexOf(key);
        this.searchHistory.splice(i, 1);
        this.searchHistory.unshift(key);
      }
      while (this.searchHistory.length > 5) {
        this.searchHistory.pop();
      }
      localStorage.setItem("historyList", JSON.stringify(this.searchHistory));
    },
    createFilter(queryString) {
      return (history) => {
        return (
          history.value.toLowerCase().indexOf(queryString.toLowerCase()) === 0
        );
      };
    },
    initSearchHistory() {
      if (JSON.parse(localStorage.getItem("historyList"))) {
        this.searchHistory = JSON.parse(localStorage.getItem("historyList"));
      }
    },
    handleDebounce: Debounce(function () {
      this.querySearchAsync(this.queryParams.keyWord);
    }),
    handleReIndex(row) {
      row.loading = true;
      this.reIndex(row);
    },
    handleRemoveIndex(params) {
      if (!this.indexToDel || params.id != this.indexToDel) {
        this.$message({
          message: this.$t('message.confirm_delete_index_message'),
          type: "warning",
        });
        this.indexToDel = params.id;
        return;
      }
      this.indexToDel = null;
      this.$http
        .post("/index/removeIndex",
          "id=" + params.id
        )
        .then((res) => {
          if (res.data.code == 0) {
            this.queryIndex();
          }
        });
    },
    indexLoad(tab, event) {
      if (tab.name == "indexDirectoryTab") {
        this.queryIndex();
      }
      if (tab.name == "searchConfigTab") {
        this.getBaseConfig();
      }
    },
    showConfig() {
      this.configVisible = true;
    },
    showAddIndex() {
      this.addIndexVisible = true;
    },
    sortChange() { },
    selectRow(row) {
      this.currentRow = row;
      for (var i = 0; i < this.$refs.Table.tableData.length; i++) {
        if (this.$refs.Table.tableData[i].id == row.id) this.currentIndex = i;
      }
    },
    dblOpen(row) {
      this.selectRow(row);
      var fileType = row.fileType;
      if (fileType == "视频" || fileType == "音频" || fileType == "图片") {
        this.viewFile(row);
      } else {
        if (row.remote) window.open("/file/viewFile?id=" + row.id);
        else
          this.openLocalFile(
            row.id,
            row.path + (row.path.endsWith("\\") ? "" : "\\") + row.fileName
          );
      }
    },
    querySearchAsync(key) {
      this.showMoreVisible = false;
      if (!key) {
        document.title = "Search Everything";
        this.queryRecent();
        return;
      }
      this.$http
        .get("/file/quickSearch", {
          params: {
            key:
              this.queryParams.keyWord == ""
                ? undefined
                : this.queryParams.keyWord,
            fileType:
              this.filterTypeRadio == "" ? undefined : this.filterTypeRadio,
            top: 7,
          },
        })
        .then((res) => {
          if (res.data.beans.length == 7) this.showMoreVisible = true;
          this.pager = res.data;
        });
    },
    openLocalFile(fileId, path) {
      this.$http
        .get("/file/openFile", {
          params: {
            path: path == "" ? undefined : path,
            fileId: fileId == "" ? undefined : fileId,
          },
        })
        .then((res) => {
          if (res.data.code == -1) {
            this.$message({
              message: res.data.msg,
              type: "error",
            });
            this.pager.beans.splice(
              this.pager.beans.indexOf(this.currentRow),
              1
            );
          }
        });
    },
    openParentFile(path) {
      this.$http
        .get("/file/openDIR", {
          params: {
            path: path == "" ? undefined : path,
          },
        })
        .then((res) => { });
    },
    queryRecent() {
      this.$http
        .get("/file/recent", {
          params: {
            top: this.queryParams.top == "" ? undefined : this.queryParams.top,
            fileType:
              this.filterTypeRadio == "" ? undefined : this.filterTypeRadio,
          },
        })
        .then((res) => {
          this.pager = res.data;
        });
    },
    query(key) {
      if (!key) {
        document.title = "Search Everything";
        this.queryRecent();
        return;
      }
      this.showMoreVisible = false;
      this.putSearchHistory(key);
      document.title = key + "_Search";
      this.$http
        .get("/file/search", {
          params: {
            top: this.queryParams.top == "" ? undefined : this.queryParams.top,
            key:
              this.queryParams.keyWord == ""
                ? undefined
                : this.queryParams.keyWord,
            fileType:
              this.filterTypeRadio == "" ? undefined : this.filterTypeRadio,
          },
        })
        .then((res) => {
          this.pager = res.data;
        });
    },
    queryIndex() {
      this.$http
        .get("/index/getList", {
          params: {},
        })
        .then((res) => {
          this.indexPager = res.data;
        });
    },
    reIndex(params) {
      this.$http
        .post("/index/reIndex",
          "id=" + params.id
        )
        .then((res) => {
          if (res.data.code == 0) {
            this.queryIndex();
          }
        });
    },
    createIndex(params) {
      params = {
        path: this.addIndexParams.path,
        type: this.addIndexParams.type,
        listen: this.addIndexParams.listen,
        loading: true,
      };
      this.addIndexVisible = false;

      this.$http
        .post("/index/create",
          "path=" + (params.path == "" ? undefined : params.path) +
          "&type=" + (params.type == "" ? undefined : this.addIndexParams.type) +
          "&listen=" + (this.addIndexParams.listen == undefined ? false : this.addIndexParams.listen) +
          "&userName=" +
          (this.addIndexParams.userName == ""
            ? undefined
            : this.addIndexParams.userName) +
          "&passwd=" +
          (this.addIndexParams.passwd == ""
            ? undefined
            : this.addIndexParams.passwd)
        )
        .then((res) => {
          if (res.data.code == 0) {
            this.indexPager.beans.push(params);
            this.queryIndex();
            this.resetAddIndexForm();
          }
        });
    },
    resetAddIndexForm() {
      this.addIndexParams = { type: "LOCAL" };
    },
    resetUpdateUserForm() {
      this.updateUserParams.oldPass = "";
      this.updateUserParams.newPass = "";
    },
    handleCommand(commnad) {
      if (commnad == "open")
        this.openLocalFile(
          this.currentRow.id,
          this.currentRow.path +
          (this.currentRow.path.endsWith("\\") ? "" : "\\") +
          this.currentRow.fileName
        );
      if (commnad == "openDIR")
        this.openParentFile(
          this.currentRow.path +
          (this.currentRow.path.endsWith("\\") ? "" : "\\") +
          this.currentRow.fileName
        );
      if (commnad == "searchFolder") {
        this.queryParams.keyWord = "path:" + this.currentRow.path;
        this.refreshPage();
      }
      if (commnad == "downloadFile")
        window.open("/file/downloadFile?ticket=" + this.currentRow.id);
      if (commnad == "viewFile") this.viewFile(this.currentRow);
      if (commnad == "httpStream") this.httpStream(this.currentRow);
      if (commnad == "shareFile") this.shareFile(this.currentRow, false, false);
      if (commnad == "shareFile1") this.shareFile(this.currentRow, true, false);
    },
    httpStream(row) {
      this.shareFile(row, true, true);
    },
    viewFile(row) {
      var url = "/file/viewFile?id=" + row.id;
      if (row.fileType == "视频") {
        this.videoVisible = true;
      } else if (row.fileType == "音频") {
        this.audioVisible = true;
      } else if (row.fileType == "图片") {
        this.createImageList();
      } else {
        window.open(url);
      }
    },
    shareFile(row, accessControl, isHttpStream) {
      this.$http
        .get("/share/shareFile", {
          params: {
            fileId: row.id == "" ? undefined : row.id,
            accessControl: accessControl,
          },
        })
        .then((res) => {
          if (res.data.code == 0) {
            var content = "";
            if (isHttpStream) {
              content =
                location.protocol +
                "//" +
                location.host +
                "/file/stream?id=" +
                res.data.bean.code;
              if (accessControl) {
                content += "&fetchCode=" + res.data.bean.fetchCode;
              }
            } else {
              content =
                location.host + "/file/download?ticket=" + res.data.bean.code;
              if (accessControl) {
                content =
                this.$t('message.share_link')
                  +
                  content +
                  this.$t('message.fetch_code') +
                  res.data.bean.fetchCode;
              }
            }
            this.copyText(content);
          } else {
            this.$message({
              message: res.data.msg,
              type: "error",
            });
          }
        });
    },
    handleSizeChange(val) {
      this.query(val);
    },
    convert(limit) {
      var size = "";
      if (limit < 1 * 1024) {
        //如果小于0.1KB转化成B
        size = limit.toFixed(2) + "B";
      } else if (limit < 1 * 1024 * 1024) {
        //如果小于0.1MB转化成KB
        size = (limit / 1024).toFixed(2) + "KB";
      } else if (limit < 1 * 1024 * 1024 * 1024) {
        //如果小于0.1GB转化成MB
        size = (limit / (1024 * 1024)).toFixed(2) + "MB";
      } else {
        //其他转化成GB
        size = (limit / (1024 * 1024 * 1024)).toFixed(2) + "GB";
      }
      var sizestr = size + "";
      var len = sizestr.indexOf(".");
      var dec = sizestr.substr(len + 1, 2);
      if (dec == "00") {
        //当小数点后为00时 去掉小数部分
        return sizestr.substring(0, len) + sizestr.substr(len + 3, 2);
      }
      return sizestr;
    },
    refreshPage() {
      this.query(this.queryParams.keyWord);
    },
  },
};
</script>
